@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,300;0,400;0,500;1,500;1,700&family=Poppins:wght@300;600&display=swap');
:root {
  --color-bg:#1f1f38;
  --coloor-bg-variant:#2c2c6c;
  --color-primary:#4db5ff;
  --color-white:#fff;
  --color-light:rgba(255,255,255,0.6);

  --container-width-lg: 75%;
  --container-width-md:85%;
  --conatiner-width-sm:90%;
  --transition: all 400ms ease;
}


body {
  /* margin: 0; */
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
    /* font-family: 'Montserrat', sans-serif; */
font-family: 'Poppins', sans-serif;
  /* -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
  /* background-color: black;
  color: white; */
  background: var(--color-bg);
  color: var(--color-white);
  line-height:1.7;
  background-image: url(../src/assests/bg-texture.png);
}
*{
  margin:0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}


html{
  scroll-behavior: smooth;
}

::-webkit-scrollbar{
  display: none;
}



code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,h2,h3,h4,h5{
  font-weight: 500;
}

h1{
  font-size: 2.5rem;
}

section{
  margin-top: 8rem;
}

section>h2, section>h5{
  text-align: center;
  color: var(--color-light);
}

section>h2{
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light{
  color:var(--color-light);
}

a{
  color: var(--color-primary);
  transition:var(--transition);
}
a:hover{
  color: var(--color-white);
}

.btn{
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75 rem 1.2rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover{
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary{
 background: var(--color-primary);
 color: var(--color-bg); 
}

img{
  display: block;
  width: 100%;
  object-fit: cover;
}

/* medium device */

@media screen and (max-width:1024px) {
  .container{
    width: var(--container-width-md);
  }

  section{
    margin-top: 6rem;
  }
}

@media screen and (max-width:600px) {
  .container{
    width: var(--container-width-sm);
  }

  section > h2{
    margin-bottom: 2rem;
  }
}